<template>
	<view>
		<view class="index-equipment-tab">
			<u-sticky>
				<view class="filter-base">
					<view class="base-equip" v-for="item in base_equipList" :key="item.equipId"
						:class="{selected:selected_base_equip==item.equipId}">
						<u--image class='img' :showLoading="true" :src="item.imagePath" width="8vw" height="8vw"
							@click="baseClick(item.equipId)">
						</u--image>
					</view>
				</view>
			</u-sticky>
			<view class="equipmentlist-ul-wrap">
				<view class="equipmentlist-ul">
					<view class="equipment-item" v-for="(item,index) in show_equipment_list" :key="item.equipId">
						<view class="item-top">

							<view class="pic" :style="'background-image:url('+item.imagePath+')'">
							</view>


							<view class="name">{{item.name}}</view>
						</view>
						<view class="item-effect">
							<view class="part-title">效果</view>
							<view class="equipment-effect">{{item.effect}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		options
	} from '@/common/options.js'
	import {
		_http
	} from '@/utils/request.js'
	import _ from 'lodash'
	export default {
		data() {
			return {
				//装备列表
				equipList: [],
				//基础装备
				base_equipList: [],
				//当前被选中的合成件
				selected_base_equip: null,
				//显示列表
				show_equipment_list: [],
			};
		},
		methods: {
			//根据id获取装备信息
			getEquip(id) {
				this.equipList.forEach((item, index) => {
					if (item.equipId == id) {
					}
				})
			},
			/**通过基础装备搜索装备 */
			filterEquipmentByFormula: function(formula, equip_list) {
				var rs;
				if (!formula) {
					rs = null;
				} else {
					rs = _.filter(equip_list, function(equipment) {
						return _.includes(equipment.formula, formula) || +equipment.equipId === +formula;
					});
				}
				rs = _.orderBy(rs, "type");
				return rs;
			},
			//单击基础装备筛选
			baseClick(id) {
				if (this.selected_base_equip == id) {
					this.selected_base_equip = null
					this.show_equipment_list = _.orderBy(this.equipList, "type")
					return
				}
				this.selected_base_equip = id
				this.show_equipment_list = this.filterEquipmentByFormula(id, this.equipList);
			},


		},
		onLoad() {
			//深拷贝
			function deepClone(obj) {
				let copy = obj instanceof Array ? [] : {}
				for (let key in obj) {
					if (obj.hasOwnProperty(key)) {
						copy[key] = typeof obj[key] === 'object' ? deepClone(obj[key]) : obj[key]
					}
				}
				return copy
			}
			this.equipList = deepClone(options.equipList)
			//S7赛季的装备
			const s7_equip = [501, 502, 503, 504, 505, 506, 507, 508, 509, 519, 521, 523, 525, 529, 531, 535, 537, 539,
				541, 543, 545, 547, 551, 553, 555, 557, 559, 561, 565, 567, 569, 571, 573, 577, 579, 581, 583, 587,
				589, 591, 595, 597, 601, 603, 607, 412, 413, 414, 415, 416, 417, 418, 419, 420, 421, 6022, 7001, 7002,
				7003, 7004, 7005, 7006, 7007, 7008, 7009, 7010, 7011, 7012, 7013, 7014, 7015, 7016, 7017, 7018, 7019,
				7020, 7031, 7032, 7022, 7023, 7024, 7025, 7026, 7027, 7028, 7030, 5001, 5002, 5003, 5004, 5005, 5006,
				5007, 5008, 5009, 5010, 5011, 5012, 5013, 5014, 5015, 5016, 5017, 5018, 5019, 5020, 5021, 5022, 5023,
				5024, 5025, 5026, 5027, 5028, 5029, 5030, 5031, 5032, 5033, 5034, 5035, 5036
			];
			//从所有装备中过滤出S7赛季的装备
			this.equipList = _.filter(this.equipList, function(item) {
				return _.indexOf(s7_equip, +item.equipId) !== -1;
			});
			//S7赛季的特殊装备（光明、转职、金鳞龙装备）
			const s7_spec = [412, 413, 414, 415, 416, 417, 418, 419, 420, 421, 6022, 7001, 7002, 7003, 7004, 7005, 7006,
				7007, 7008, 7009, 7010, 7011, 7012, 7013, 7014, 7015, 7016, 7017, 7018, 7019, 7020, 7031, 7032, 7022,
				7023, 7024, 7025, 7026, 7027, 7028, 7030, 5001, 5002, 5003, 5004, 5005, 5006, 5007, 5008, 5009, 5010,
				5011, 5012, 5013, 5014, 5015, 5016, 5017, 5018, 5019, 5020, 5021, 5022, 5023, 5024, 5025, 5026, 5027,
				5028, 5029, 5030, 5031, 5032, 5033, 5034, 5035, 5036
			];
			//将特殊装备设置type为3
			_.forEach(this.equipList, item => {
				if (_.indexOf(s7_spec, +item.equipId) !== -1) {
					item.type = 3;
				};
			});
			//基础装备
			_.forEach(this.equipList, item => {
				if (item.type == 1) {
					this.base_equipList.push(item)
				}
			})
			this.show_equipment_list = _.orderBy(this.equipList, "type")
			console.log(this.show_equipment_list);

		}
	}
</script>

<style lang="scss">
	.index-equipment-tab {
		.filter-base {
			display: flex;
			flex-direction: row;
			justify-content: center;
			background-color: #ffffff;
			padding: 2vh 0;
			border-bottom: 1px solid #eff2f2;

			.base-equip {
				margin-right: 2vw;
				border: 1px solid #83a2be;

			}

			.base-equip:last-child {
				margin: 0;
			}

			.selected {
				// border: 3px solid #27a9a2;
				outline: 2px solid #27a9a2;
			}
		}

		.equipmentlist-ul-wrap {
			// border-top: #eff2f2 1vh solid;
			background-color: #f0f3f7;

			.equipmentlist-ul {
				.equipment-item {
					margin-bottom: 4vw;
					background-color: #ffffff;
					padding: 2vh 2vw;

					.item-top {
						display: flex;
						flex-direction: row;
						align-items: center;

						.pic {
							background-repeat: no-repeat;
							background-size: contain;
							width: 10vw;
							height: 10vw;
							background-color: #1c1c1c;
							border-radius: 1vw;
							// 记录一下，给图片加内边框
							outline: 1px solid black;
							outline-offset: -1px;
							margin-left: 4vw;
						}

						.name {
							margin-left: 4vw;
							font-size: 4vw;
						}
					}

					.item-effect {
						margin-left: 18vw;

						.part-title {
							font-size: 3vw;
						}

						.equipment-effect {
							font-size: 3vw;
							color: #757e7f;
						}
					}
				}
			}
		}
	}
</style>
